@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.grid {
  position: absolute;
  bottom: 14%;
  left: 12%;
}

.button-BFS {
  font-family: "Lato";
  font-size: medium;
  position: absolute;
  width: 250px;
  height: 30px;
  left: 45%;
  top: 30%;
  transition-duration: 0.4s;
  border-radius: 12px;
  outline: none;
}

.button-DFS {
  font-family: "Lato";
  font-size: medium;
  position: absolute;
  width: 250px;
  height: 30px;
  left: 25%;
  top: 30%;
  transition-duration: 0.4s;
  border-radius: 12px;
  outline: none;
}

.button-reset {
  font-family: "Lato";
  font-size: medium;
  position: absolute;
  width: 250px;
  height: 30px;
  left: 5%;
  top: 30%;
  transition-duration: 0.4s;
  border-radius: 12px;
  outline: none;
}

.button-BFS:hover {
  background-color: #4c56af; /* Green */
  color: white;
}

.button-DFS:hover {
  background-color: #4c56af; /* Green */
  color: white;
}

.button-reset:hover {
  background-color: #4c56af; /* Green */
  color: white;
}

.top-container {
  position: absolute;
  width: 100%;
  height: 10%;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(117, 9, 121, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
}

.node-container {
  left: 50%;
  width: 10%;
  top: 30%;
}

.node {
  width: 25px;
  height: 25px;
  outline: 5px round rgb(175, 216, 248);
  display: inline-block;
  margin: 0 2px;
  border-radius: 6px;
  transition-duration: 1s;
}

.node {
  width: 25px;
  height: 25px;
  outline: 5px round rgb(175, 216, 248);
  display: inline-block;
  margin: 0 2px;
  border-radius: 6px;
  transition-duration: 0.5s;
}

